.InfoContent {
  display: flex;
  flex-direction: row;
  height: 30%;
  background-color: #007aff;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}

.InfoContent > div {
  width: 50%;
  margin-top: 2%;
  margin-bottom: 2%;
  display: flex;
  flex-direction: column;
  color: white;
}

.InfoContent > div > div {
  margin-top: 1%;
  margin-left: 2%;
}

.InfoContent > div > img {
  margin-top: 10px;
  margin-left: 10px;
  width: 10%;
  height: 10%;
}
