.Course {
  display: flex;
  flex-direction: row;
  overflow-x: hidden;
  background-color: #f6f7eb;
  margin-right: 2%;
}

.Heading {
  display: flex;
  flex-direction: row;
  background-color: #f6f7eb;
  margin-top: 1%;
  font-size: 2vw;
  color: #007aff;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}

.AllCourseButton {
  position: absolute;
  right: 1%;
  height: 6%;
  margin-top: 0.2%;
}
.CourseCard {
  margin-top: 0.2%;
  margin-right: 5%;
  margin-bottom: 2%;
  margin-left: 0.5%;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  width: 16rem;
  height: 16rem;
  border: 2px solid #007aff;
  border-radius: 1%;
}

.CardImage {
  width: 100%;
  height: 40%;
}

.CardText {
  width: 14rem;
  height: 30%;
}

.CourseButton {
  margin-left: 2%;
}
